<template>
  <div class="date-type-wrapper">
    <popup class="filter-popup-wrapper">
      <template #header>
        <button class="filter-btn">
          <!--<fa :icon="['far', 'file-lines']" />-->
          <span id="date-selection">Date Range</span><fa icon="angle-down" />
        </button>
      </template>
      <template #content>
        <div class="display-selection-wrapper">
          <span v-for="type in types" :key="type.id">
            <input type="radio" name="date_type" :value="type.name" v-model="checkedDateType" @change="selectedDateType" :id="type.id" :checked="currentDateType == type.name" />
            <fa class="checkmark" icon="check" />
            <label :for="type.id">{{ type.name }}</label>
          </span>
        </div>
      </template>
    </popup>
  </div>
</template>

<script>
/* eslint-disable */
import moment from "moment";
import Popup from "@/components/Popup.vue";

export default {
  components: {
    Popup,
  },
  props: {
    currentStartDate: {
      type: [String, Number, Date],
      default: ""
    },
    currentEndDate: {
      type: [String, Number, Date],
      default: ""
    },
    defaultDateType: {
      type: String,
      default: "Date Range"
    }, 
  },
  data() {
    return {
      types: [
        {
          id: "single",
          name: "Single Date"
        },
        {
          id: "range",
          name: "Date Range"
        },
        {
          id: "today",
          name: "Today"
        },
        {
          id: "week",
          name: "This Week"
        },
      ],
      currentDateType: this.defaultDateType
    };
  },
  watch: {
    defaultDateType: function defaultDateType() {
      this.currentDateType = this.defaultDateType;
    },
    currentStartDate: function currentStartDate() {
      const currentStartDate = moment(this.currentStartDate).format("MMM DD, yyyy");
      const currentEndDate = moment(this.currentEndDate).format("MMM DD, yyyy");

      const today = moment().startOf('day').format("MMM DD, yyyy");
      const weekStart = moment().startOf('week').format("MMM DD, yyyy");
      const weekEnd = moment().endOf('week').format("MMM DD, yyyy");

      if (this.currentDateType == 'Today'){
        if (currentStartDate != today) {
          this.currentDateType = 'Single Date';
        }
      } else if (this.currentDateType == 'This Week'){
        if (currentStartDate != weekStart) {
          this.currentDateType = 'Date Range';
        }
      } else if (this.currentDateType == 'Date Range') {
        if (currentStartDate == weekStart && currentEndDate == weekEnd) {
          this.currentDateType = 'This Week';
        }
      } else if (this.currentDateType == 'Single Date') {
        if (currentStartDate == today) {
          this.currentDateType = 'Today';
        }
      }
      var value = this.currentDateType,
        selected = document.querySelector('input[name="date_type"]:checked'),
        selection = document.querySelector("#date-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    currentEndDate: function currentEndDate() {
      const currentStartDate = moment(this.currentStartDate).format("MMM DD, yyyy");
      const currentEndDate = moment(this.currentEndDate).format("MMM DD, yyyy");

      const weekEnd = moment().endOf('week').format("MMM DD, yyyy");
      const weekStart = moment().startOf('week').format("MMM DD, yyyy");
      if (this.currentDateType == 'This Week'){
        if (currentEndDate != weekEnd) {
          this.currentDateType = 'Date Range';
        }
      } else if(this.currentDateType == 'Date Range') {
        if (currentStartDate == weekStart && currentEndDate == weekEnd) {
          this.currentDateType = 'This Week';
        }
      }
      var value = this.currentDateType,
        selected = document.querySelector('input[name="date_type"]:checked'),
        selection = document.querySelector("#date-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    }
  },
  methods: {
    selectedDateType() {
      if (this.currentDateType == "Single Date") {
        this.$emit('changeStartDate', "")
      } else if (this.currentDateType == 'Date Range') {
        this.$emit('changeStartDate', "")
        this.$emit('changeEndDate', "")
      } else if (this.currentDateType == 'Today') {
        const today = moment().startOf("day").toDate();
        this.$emit('changeStartDate', today)
      } else if (this.currentDateType == 'This Week') {
        const weekStart = moment().startOf('week').toDate();
        const weekEnd = moment().endOf('week').toDate();
        this.$emit('changeStartDate', weekStart)
        this.$emit('changeEndDate', weekEnd)
      }
      
      var value = "Nothing selected",
        selected = document.querySelector('input[name="date_type"]:checked'),
        selection = document.querySelector("#date-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
  },
  computed: {
    checkedDateType: {
      get() {
        return this.currentDateType;
      },
      set(newType) {
        this.currentDateType = newType;
        this.$emit("changeDateType", newType);
      },
    },
  }
};
</script>
