<template>
  <div class="dept-wrapper container">
    <div v-if="loading" class="loading-overlay">
      <div class="overlay-content"><img src="@/assets/img/loading.gif" style="width: 60px" /></div>
    </div>
    <Header />
    <div class="content-wrapper">
      <div class="filter-toggle-wrapper" ref="toggleFilter">
        <span>
          <button class="filter-toggle" @click="toggleFilter()" :class="isFilterActive ? 'collasped' : ''"><fa :icon="['fas', 'sliders']"></fa></button>
        </span>
        <div class="filter-wrapper" :class="[isFilterActive ? 'slide-in' : 'slide-out']">
          <div class="filter-content-wrapper">
            <popup class="filter-popup-wrapper stores-popup">
              <template #header>
                <button class="filter-btn">
                  <!--<fa icon="store" />--> <span id="store-selection">{{ selectedCompanies.companyName }}</span><fa icon="angle-down" />
                </button>
              </template>
              <template #content>
                <div class="display-selection-wrapper">
                  <span v-for="company in companies" :key="company.id">
                    <input type="radio" name="store_type" :id="company.id" :value="company" v-model="selectedCompanies" @change="selectedStore" />
                    <fa class="checkmark" icon="check" />
                    <label :for="company.id">{{ company.companyName }}</label>
                  </span>
                </div>
              </template>
            </popup>
            <div class="filter-header">
              <popup class="filter-popup-wrapper">
                <template #header>
                  <button class="filter-btn"><!--<fa :icon="['far', 'file-lines']" />--><span id="selection">Analysis Department</span><fa icon="angle-down" /></button>
                </template>
                <template #content>
                  <div class="display-selection-wrapper">
                    <span>
                      <input type="radio" name="dept_type" value="Standard" v-model="selectedRptType" @change="selectedType" id="regular" checked="checked" />
                      <fa class="checkmark" icon="check" />
                      <label for="regular">Standard Department</label>
                    </span>
                    <span>
                      <input type="radio" name="dept_type" value="Analysis" v-model="selectedRptType" @change="selectedType" id="analysis" />
                      <fa class="checkmark" icon="check" />
                      <label for="analysis">Analysis Department</label>
                    </span>
                  </div>
                </template>
              </popup>
              <button class="search-btn btn btn-square" @click="searchWithFilter()"><fa icon="magnifying-glass"></fa></button>
            </div>

            <DateType 
              @changeDateType="dateType = $event"
              @changeStartDate="startDate = $event"
              @changeEndDate="endDate = $event"
              :currentStartDate="startDate"
              :currentEndDate="endDate" />

            <Transition name="fade">
              <p v-if="invalidDateRange" class="invalid-search-msg">
                <fa icon="triangle-exclamation" />
                <span>Invalid date range</span>
              </p>
            </Transition>

            <div v-if="dateType == 'Single Date' || dateType == 'Today'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" labelEnableText="Date" />
            </div>
            <div v-if="dateType == 'Date Range' || dateType == 'This Week'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" :startDate="endDate" labelEnableText="From" />
              <DatePicker @changeDateValue="endDate = $event" :defaultDate="endDate" :startDate="startDate" :alwaysStartDate="true" labelEnableText="To" />
            </div>

            <div v-if="selectedRptType == 'Standard'" class="filter-content-wrapper">
              <label class="filter-label">Search By</label>
              <label><span>Stn # </span><input type="text" v-model="stn" /></label>
              <label><span>Cashier #</span><input type="text" v-model="cashier" /></label>
            </div>
          </div>
        </div>
      </div>

      <div class="content" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 90px)' }">
        <div v-if="standardChart">
          <div v-if="!empty && depts.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
          <div v-if="!empty && depts.length > 0" class="dept-table-wrapper">
            <DataTable id="rpt-table" class="dept-tb" :value="depts" :exportFilename="fileName" ref="dt">
              <template #header>
                <div class="table-header-wrapper">
                  <div class="table-header">
                    <h3>{{ $t('header.' + this.$route.name) }} - {{ storeName }}</h3>
                    <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                    <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                  </div>
                  <div class="table-btn-group">
                    <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                    <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv"></fa></button>
                  </div>
                </div>
              </template>
              <Column field="dep_name" header="Department">
                <template #body="slotProps">{{ slotProps.data.dep_no + " " + slotProps.data.dep_name }}</template>
              </Column>
              <Column field="amount" header="Amount ($)" class="amount">
                <template #body="slotProps">{{ formatPrice(slotProps.data.amount) }}</template>
              </Column>
              <Column field="percentage" header="%" class="percentage">
                <template #body="slotProps">{{ formatPrice(slotProps.data.percentage) }}%</template>
              </Column>
              <ColumnGroup type="footer">
                <Row>
                  <Column footer="Total" footerStyle="text-align:right" />
                  <Column :footer="formatPrice(deptsTotal)" footerStyle="text-align:right" />
                  <Column footer="" />
                </Row>
              </ColumnGroup>
            </DataTable>
            <div class="dept-total">
              <ul>
                <li class="bold-600">Department Total($): <span class="bold-600">{{ formatPrice(deptsTotal) }}</span></li>
                <li>Member & VIP Discount($): <span>-{{ formatPrice(deptsDiscount) }}</span></li>
                <li>Taxes($): <span>{{ formatPrice(deptsTax) }}</span></li>
                <li>Total Accounted For($):<span class="bold-600">{{ formatPrice(deptsTotal - deptsDiscount + deptsTax) }}</span></li>
              </ul>
              <div class="doughnut-chart" v-if="depts.length > 0">
                <VueApexCharts :options="setDoughnutChartOptions" :series="deptsAmount" />
              </div>
            </div>
          </div>
        </div>

        <div v-if="analysisChart">
          <div v-if="!empty && analysisDepts.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
          <div v-if="!empty && analysisDepts.length > 0" class="dept-table-wrapper">
            <DataTable id="rpt-table" class="dept-tb" v-model:expandedRows="state.expandedRows" dataKey="dep_no" :value="analysisDepts" :exportFilename="fileName" ref="dt">
              <template #header>
                <div class="table-header-wrapper">
                  <div class="table-header">
                    <h3>{{ $t('header.' + this.$route.name) }} - {{ storeName }}</h3>
                    <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                    <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                  </div>
                  <div class="table-btn-group">
                    <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                    <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-excel"></fa></button>
                  </div>
                </div>
              </template>
              <Column field="dep_name" header="Department">
                <template #body="slotProps">
                  <p class="first-col">{{ slotProps.data.dep_no + " " + slotProps.data.dep_name }}</p>
                  <a class="more-info-btn" @click.stop="onExpandRow(slotProps.data.dep_no)">
                    <span :class="[state.expandedRows[slotProps.data.dep_no] ? 'expand' : '']">
                      <button class="expand-btn"><fa icon="caret-right" /></button>
                    </span>
                    {{ slotProps.data.dep_no + " " + slotProps.data.dep_name }}
                  </a>
                </template>
              </Column>
              <Column field="amount" class="amount">
                <template #header><p>Net Item Sales<br />Amount ($)</p></template>
                <template #body="slotProps">{{ formatPrice(slotProps.data.amount) }}</template>
              </Column>
              <Column field="amt_percent" header="%" class="percentage">
                <template #body="slotProps">{{ formatPrice(slotProps.data.amt_percent) }}%</template>
              </Column>
              <Column field="trn_dep_cnt" class="count">
                <template #header><p>Transactions<br />Count</p></template>
                <template #body="slotProps">{{ formatNumber(slotProps.data.trn_dep_cnt) }}</template>
              </Column>
              <Column field="cnt_percent" header="%" class="percentage">
                <template #body="slotProps">{{ formatPrice(slotProps.data.cnt_percent) }}%</template>
              </Column>

              <template #expansion="slotProps">
                <div class="more-info-wrapper">
                  <p><span class="bold-600">Transactions Count</span> {{ formatNumber(slotProps.data.trn_dep_cnt) }}</p>
                  <p><span class="bold-600">%</span> {{ formatPrice(slotProps.data.cnt_percent) }}%</p>
                </div>
              </template>

              <ColumnGroup type="footer">
                <Row>
                  <Column class="dt-footer" footer="Total" footerStyle="text-align:right" />
                  <Column class="dt-footer" :footer="formatPrice(analysisDeptsTax + analysisDeptsTotal)" footerStyle="text-align:right" />
                  <Column class="dt-footer" :footer="formatNumber(totalAnalysisTransCount)" :colspan="2" footerStyle="text-align:right" />
                  <Column class="dt-footer" footer="" />
                </Row>
              </ColumnGroup>
              <template #footer>
                <div class="mobile-dt-footer">
                  <p class="bold-600">Total Amount($): {{ formatPrice(analysisDeptsTax + analysisDeptsTotal) }}</p>
                  <p class="bold-600">Number of Transactions: {{ formatNumber(totalAnalysisTransCount) }}</p>
                </div>
              </template>
            </DataTable>
            <div class="dept-total">
              <div>
                <ul>
                  <li class="bold-600">Department Total($): <span class="bold-600">{{ formatPrice(analysisDeptsData.sale_total) }}</span></li>
                  <li>Bottle Deposits($): <span>{{ formatPrice(analysisDeptsData.bottle_dept) }}</span></li>
                  <li>Member & VIP Discount($): <span>{{ formatPrice(analysisDeptsData.members_discnt) }}</span></li>
                  <li>Taxes($): <span>{{ formatPrice(analysisDeptsData.tax) }}</span></li>
                  <li>Surcharge($): <span>{{ formatPrice(analysisDeptsData.subchg) }}</span></li>
                  <li class="bold-600">Net Amount($):<span class="bold-600"> {{ formatPrice(analysisDeptsData.net_amount) }}</span></li>
                </ul>
                <ul>
                  <li>Misc Trans Items($): <span>{{ formatPrice(analysisDeptsData.misc_amt) }}</span></li>
                  <li>Coupons($): <span>{{ formatPrice(analysisDeptsData.coupon) }}</span></li>
                  <li>Reward($): <span>{{ formatPrice(analysisDeptsData.reward) }}</span></li>
                  <li>Redemption($): <span>{{ formatPrice(analysisDeptsData.redemption) }}</span></li>
                  <li class="bold-600">Total Accounted For($):<span class="bold-600">{{ formatPrice(analysisDeptsData.total_accounted) }}</span></li>
                </ul>
              </div>
              <VueApexCharts height="600" :options="setBarChartOptions" :series="setBarChartData" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive } from "vue";
import axios from "axios";
import moment from "moment";
import Header from "@/components/Header.vue";
import Popup from "@/components/Popup.vue";
import DatePicker from "@/components/DatePicker.vue";
import DateType from "@/components/DateType.vue";

export default {
  components: {
    Header,
    Popup,
    DatePicker,
    DateType
  },
  setup() {
    const state = reactive({
      expandedRows: {},
    });

    const onExpandRow = (id) => {
      const checkProp = id in state.expandedRows;
      if (checkProp) {
        delete state.expandedRows[id];
      } else {
        state.expandedRows[id] = true;
      }
      state.expandedRows = { ...state.expandedRows };
    };

    return {
      state,
      onExpandRow,
    };
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      isFilterActive: true,
      empty: true,
      companies: [
      {
        id: 1027,
        companyName: "Apple Supermarket 1"
        },
        {
        id: 1221,
        companyName: "Apple Supermarket 2"
        },
      ],
      invalidStore: false,
      selectedCompanies: [],
      storeName: "",
      loading: false,
      depts: [],
      deptsId: [],
      deptsAmount: [],
      deptsDisplay: [],
      deptsTax: "",
      deptsTotal: "",
      analysisDeptsData: [],
      analysisDepts: [],
      analysisDeptsDisplay: [],
      analysisDeptsId: [],
      analysisDeptsAmount: [],
      analysisDeptsTransCount: [],
      analysisDeptsTax: "",
      analysisDeptsTotal: "",
      analysisDeptsTransCountTotal: "",
      startDate: "",
      endDate: "",
      rptStartDate: "",
      rptEndDate: "",
      selectedRptType: "Analysis",
      dateType: "Date Range",
      standardChart: false,
      analysisChart: true,
      stn: "",
      cashier: "",
      invalidDateRange: false,
      singleDateActive: false,
      dateRangeActive: false
    };
  },
  mounted() {
    // this.companies = JSON.parse(localStorage.getItem("companies"));

    this.selectedCompanies = this.companies[0];

    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },
  // beforeMount() {
  //   window.addEventListener('click', this.close);
  // },
  // unmounted() {
  //   window.removeEventListener('click', this.close);
  // },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    toggleFilter() {
      this.isFilterActive = !this.isFilterActive;
    },
    // close(e) {
    //   if (!this.$refs.toggleFilter.contains(e.target)) {
    //     this.isFilterActive = false;
    //   }
    // },
    formatPrice(value) {
      if (!value || value == 0) {
        return "0.00";
      }
      let val = (value / 1).toFixed(2).replace(",", ".");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatNumber(value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatDate(date) {
      return moment(date).format("MMM DD, yyyy");
    },
    exportCSV() {
      this.$refs.dt.exportCSV();
    },
    selectedType() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="dept_type"]:checked'),
        selection = document.querySelector("#selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    selectedStore() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="store_type"]:checked'),
        selection = document.querySelector("#store-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    searchWithFilter() {
      this.loading = true;
      this.isFilterActive = false;
      this.depts = [];
      this.analysisDepts = [];
      this.storeName = this.selectedCompanies.companyName;

      this.rptStartDate = "";
      this.rptEndDate = ""

      if (this.dateType == 'Single Date' || this.dateType == 'Today'){
        this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
        this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        this.singleDateActive = true;
        this.dateRangeActive = false;
      } else if (this.dateType == 'Date Range' || this.dateType == 'This Week'){
        this.dateRangeActive = true;
        this.singleDateActive = false;
        const days = Math.round((this.endDate - this.startDate) / (1000 * 60 * 60 * 24));

        if (this.startDate && this.endDate && days >= 0 ) {
          this.invalidDateRange = false;
          this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
          this.rptEndDate = moment(this.endDate).format("YYYY-MM-DD");
        } else if (days < 0) {
          this.loading = false;
          this.isFilterActive = true;
          this.invalidDateRange = true;
          this.rptStartDate = "";
          this.rptEndDate = "";
        } else {
          this.loading = false;
          this.rptStartDate = "";
          this.rptEndDate = "";
        }
      }

      if (this.selectedRptType == "Standard") {
        this.standardChart = true;
        this.analysisChart = false;

        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Department", {
            headers: {
              marketid: this.selectedCompanies.id,
            },
            params: {
              sDate: this.rptStartDate,
              eDate: this.rptEndDate,
              stn: this.stn == "" ? 0 : this.stn,
              cashier: this.cashier == "" ? 0 : this.cashier,
            },
          })
          .then((response) => {
            this.depts = response.data.datas.deptSummaries;
            this.deptsTax = response.data.datas.tax;
            this.deptsTotal = response.data.datas.total;

            for (let i = 0; i < this.depts.length; i++) {
              this.deptsId[i] = response.data.datas.deptSummaries[i].dep_no;
              this.deptsDisplay[i] = response.data.datas.deptSummaries[i].dep_name;
              this.deptsAmount[i] = response.data.datas.deptSummaries[i].amount;
            }
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          });
      } else if (this.selectedRptType == "Analysis") {
        this.standardChart = false;
        this.analysisChart = true;

        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Department/Analysis", {
            headers: {
              marketid: this.selectedCompanies.id,
            },
            params: {
              sDate: this.rptStartDate,
              eDate: this.rptEndDate,
            },
          })
          .then((response) => {
            this.analysisDeptsData = response.data.datas;
            this.analysisDepts = response.data.datas.deptSummaryAnalyses;
            this.analysisDeptsTax = response.data.datas.tax;
            this.analysisDeptsTotal = response.data.datas.sale_total;

            for (let i = 0; i < this.analysisDepts.length; i++) {
              this.analysisDeptsId[i] = response.data.datas.deptSummaryAnalyses[i].dep_no;
              this.analysisDeptsDisplay[i] = response.data.datas.deptSummaryAnalyses[i].dep_name;
              this.analysisDeptsAmount[i] = response.data.datas.deptSummaryAnalyses[i].amount;
              this.analysisDeptsTransCount[i] = response.data.datas.deptSummaryAnalyses[i].trn_dep_cnt;
            }
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          });
        }
    },
  },
  computed: {
    fileName() {
      let excelName = this.$route.name + " " + this.startDate + "-" + this.endDate;
      return excelName;
    },
    setDoughnutChartOptions() {
      return {
        chart: {
          type: "donut",
          fontFamily: 'Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif',
        },
        labels: this.deptsDisplay,
        colors: ["#72f9b4", "#07e2f6", "#00baff", "#c6e430", "#2ad4e3", "#e329e2", "#e6a4f0", "#8964e8", "#aaa0ff", "#c4e328", "#00bdf6", "#fd9106", "#efefef", "#2c990f"],
        legend: {
          position: "bottom",
          floating: true,
        },
        responsive: [
          {
            breakpoint: 1320,
            options: {
              legend: {
                // position: "right",
                floating: false,
              },
              chart: {
                height: 1000,
              },
            },
          },
        ],
        dataLabels: {
          formatter: function (val) {
            return val.toFixed(2) + "%";
          },
          style: {
            fontSize: "15px",
          },
        },
        tooltip: {
          enabled: false,
        },
        states: {
          hover: {
            filter: {
              type: "none",
            },
          },
        },
        plotOptions: {
          pie: {
            expandOnClick: false,
            donut: {
              size: "55%",
              labels: {
                show: true,
                value: {
                  fontSize: "16px",
                },
                total: {
                  show: true,
                  label: "Department Total",
                  color: "#373d3f",
                  formatter: function (w) {
                    return w.globals.seriesTotals
                      .reduce((a, b) => {
                        return a + b;
                      }, 0)
                      .toFixed(2)
                      .toString()
                      .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
                  },
                },
              },
            },
          },
        },
      };
    },
    setBarChartData() {
      return [
        {
          name: "Net Item Sales($)",
          data: this.analysisDeptsAmount,
        },
        {
          name: "Transactions Count",
          data: this.analysisDeptsTransCount,
        },
      ];
    },
    setBarChartOptions() {
      return {
        chart: {
          type: "bar",
          fontFamily: 'Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif',
          toolbar: {
            show: true,
            tools: {
              download: false,
            },
          },
        },
        colors: ["#48b99b", "#2b90e5"],
        dataLabels: {
          enabled: false,
        },
        grid: {
          show: false,
        },
        legend: {
          markers: {
            radius: 25,
          },
        },
        plotOptions: {
          bar: {
            horizontal: true,
            barHeight: "90%",
          },
        },
        stroke: {
          show: true,
          width: 0.5,
          colors: ["#fff"],
        },
        xaxis: {
          categories: this.analysisDeptsDisplay,
          labels: {
            show: false,
          },
        },
      };
    },
    deptsDiscount() {
      let total = 0;
      for (let dept of this.depts) {
        total += dept.discount;
      }
      return total;
    },
    totalAnalysisTransCount() {
      let total = 0;
      for (let analysisDept of this.analysisDepts) {
        total += analysisDept.trn_dep_cnt;
      }
      return total;
    },
  },
};
</script>
